.home-page {
  background-image: url('./images/background_home@3x.jpg'),
    url('./images/background_repeat@3x.jpg');
  background-position: top, bottom;
  background-repeat: no-repeat, repeat;

  background-size: 100%;
  display: flex;
  width: 100%;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  > .button {
    margin-top: 130vw;
    width: 272px;
    height: 56px;
    border-radius: 15px;
    background-image: linear-gradient(to bottom, #ffc800, #ff9e00);
    .title {
      font-family: SFProDisplay;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
    }
  }
  .rules {
    margin-top: 24px;
    background: transparent;
    > .title {
      font-family: SFProDisplay;
      font-size: 16px;
      text-align: center;
      color: #ffed60;
    }
  }
  .firewall {
    background-size: 100%;
    width: 100%;
  }
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_inner {
  margin: 20px 20px 20px 20px;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  > .header {
    width: 100%;
    height: 50px;
    background-image: linear-gradient(to bottom, #ffc800, #ff9e00);
    align-items: center;
    justify-content: center;
    display: flex;
    position: relative;
    > .title {
      height: 24px;
      font-size: 20px;
      text-align: center;
      color: #ffffff;
    }
    .icclose_page_black {
      position: absolute;
      width: 40px;
      height: 40px;
      left: 4px;
      > .image {
        width: 18px;
        height: 18px;
      }
    }
  }
  .content {
    padding: 23px 12px 23px 12px;
    .normal {
      font-size: 16px;
      line-height: 1.25;
      color: #101010;
      br {
        line-height: 1.5;
      }
    }
  }
}
