.point-wrapper {
  width: 152px;
  height: 37px;
  border-radius: 18.5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(to bottom, #ffc800, #ff9e00);
  display: flex;
  align-items: center;
  justify-content: center;
  > .title {
    font-family: SFProDisplay;
    font-size: 18px;
    font-weight: bold;
    color: #ffffff;
  }
}

.progressbar-container {
  display: block;
  // width: 100%;
  align-self: stretch;
  overflow: hidden;
  height: 10px;
  border-radius: 6.5px;
  background-color: #9b0012;
  margin: 10px 4vw 12px 4vw !important;
  .progressbar-progress {
    border-radius: 10px;
    width: 100%;
    transition: width 200ms ease 0s;
    height: 10px;
    background-image: linear-gradient(to left, #ffe200, #ff9e00 31%, #ff7f00);
  }
}

.status {
  display: flex;
  align-items: center;
  // flex: 1;
  align-self: stretch;
  margin: 18px 4vw 0px 4vw;
  .chicken {
    width: 38px;
    height: 38px;
    object-fit: contain;
  }
  > .text {
    font-size: 18px;
    font-weight: bold;
    color: #ffffff;
  }
  .level {
    flex: 1;
    margin: 0px 8px 0px 8px;
  }
}
.board {
  align-self: stretch;
  // background-color: #ff7f00;
  padding: 4vw 4vw 4vw 4vw;
  position: relative;
}
.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

$item-width: 21.5vw; //cal(100vw/4);

// $item-width: 25vw;

.item {
  margin: 1vw 0px 1vw 0px;
  width: $item-width !important;
  height: $item-width;
  border-radius: 20px;
  box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.2);
  background-color: #900011 !important;
  position: relative;
  // overflow: hidden;

  > .image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.item-transparent {
  box-shadow: none;
  background-color: transparent !important;
}
.item-extension {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  // background-color: #900099 !important;
}
.increase-point {
  color: #dbff00;
}
.decrease-point {
  color: #c5c5c5;
}
.point {
  font-size: 20px;
  font-weight: 900;
  text-align: center;
  position: absolute;
  top: -20px;
  right: 10px;
}
.point-enter {
  opacity: 0.3;
  transform: scale(0.5) translateY(100%);
}
.point-enter-active {
  transform: scale(1);
  opacity: 1;
  transition: all 300ms ease-in;
}
.point-exit-active {
  opacity: 0;
  transition: all 100ms ease-in;
}
.point-exit-done {
  opacity: 0;
}

.level-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  .popup-content {
    margin: 40px 22px 22px 24px;
    text-align: center;
    line-height: 1.5;
    color: #101010;
    font-size: 16px;
    > .level {
      font-weight: bold;
    }
    > .highlight {
      color: #be0016;
      font-weight: bold;
    }
  }
  > .image {
    width: 90px;
    height: 90px;
  }
}
.popup-title {
  text-align: center;
  line-height: 1.5;
  color: #101010;
  font-size: 16px;
  > .level {
    font-weight: bold;
  }
  > .highlight {
    color: #be0016;
    font-weight: bold;
  }
}

.finish-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  .finish-title {
    margin: 12px 0 12px 12px;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    color: #be0016;
  }
  > .finish-point-wrapper {
    margin: 14px 0px 24px 0px;
  }
  .banner-wrapper {
    padding: 12px 12px 12px 12px;
    width: 100%;
    .banner {
      width: 100%;
      border-radius: 10px;
    }
  }
}

.page-button {
  align-self: stretch;
  margin: 24px 10px 10px 10px;
  border-radius: 15px;
  background-color: #be0016;
  height: 44px;
  > .title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    color: #ffffff;
  }
}
.facebook {
  align-self: stretch;
  margin: 0px 10px 10px 10px;
  border-radius: 15px;
  background-color: #3a559f;
  height: 44px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
  .icon {
    position: absolute;
    left: 12px;
    width: 28px;
    height: 80%;
    object-fit: contain;
  }
  > .title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    color: #ffffff;
  }
}

